                          class="d-inline-block d-flex flex-items-center"
                          style="font-size: 24px; padding-top: 4px;"
                        >
                          <Token :space="space.key" symbolIndex="space" size="28" />
                          <span class="ml-2" v-text="space.name" />
                        </router-link>-->
          </div>
          <div class="center_top">
            <a target="_blank" :href="appUrl + '/main'">Mint</a>
            <a target="_blank" :href="appUrl + '/redeem'">Redeem</a>
            <a target="_blank" :href="appUrl + '/stake'">LP Rewards</a>

            <a target="_blank" :href="appUrl + '/value'">Earn Yield</a>
            <span style="color: #FFFFFF">Vote</span>
          </div>
          <div class="right_top" :key="web3.account">
            <!--<span v-text="'About'" class="about" @click="modalAboutOpen = true" />-->
            <template v-if="$auth.isAuthenticated.value">
              <UiTopAccount
                @click="modalOpen = true"
                :loading="app.authLoading"
              >
                <Avatar
                  :profile="web3.profile"
                  :address="web3.account"
                  size="16"
                  class="mr-n1 mr-sm-2 mr-md-2 mr-lg-2 mr-xl-2 ml-n1"
                />
                <span
                  v-if="web3.profile?.name || web3.profile?.ens"
                  v-text="web3.profile.name || web3.profile.ens"
                  class="hide-sm"
                />
                <span v-else v-text="_shorten(web3.account)" class="hide-sm" />
              </UiTopAccount>
            </template>
            <UiVoteButton
              v-if="!$auth.isAuthenticated.value"
              @click="modalOpen = true"
              :loading="loading || app.authLoading"
              :style="{height:'38px',lineHeight:'38px'}"
            >
              <span class="hide-sm" v-text="$t('connectWallet')" />
              <Icon
                name="login"
                size="20"
                class="hide-md hide-lg hide-xl ml-n2 mr-n2 v-align-text-bottom"
              />
            </UiVoteButton>
          </div>
        </div>
      </Container>
    </nav>
    <teleport to="#modal">
      <ModalAccount
        :open="modalOpen"
        @close="modalOpen = false"
        @login="handleLogin"
      />
      <ModalAbout :open="modalAboutOpen" @close="modalAboutOpen = false" />
    </teleport>
  </Sticky>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      loading: false,
      modalOpen: false,
      modalAboutOpen: false,
      appUrl: process.env.VUE_APP_SUNDER_URL
    };
  },
  computed: {
    space() {
      const key = this.domain || this.$route.params.key;
      return this.app.spaces[key] ? this.app.spaces[key] : false;
    }
  },
  created() {
    this.setTitle();
  },
  watch: {
    space() {
      this.setTitle();
    }
  },
  methods: {
    ...mapActions(["login"]),
    setTitle() {
      document.title = this.space.name ? this.space.name : "Snapshot";
    },
    async handleLogin(connector) {
      this.modalOpen = false;
      this.loading = true;
      await this.login(connector);
      this.loading = false;
    }
  }
};
</script>

