            target="_blank"
            class="float-right"
          >
            {{ _shorten(payload.vote_tx) }}
            <Icon name="external-link" class="ml-1" />
          </a>
        </div>

      </Block>


      <BlockResults
        :id="id"
        :space="space"
        :payload="payload"
        :results="results"
        :votes="votes"
      />
      <BlockActions
        :id="id"
        :space="space"
        :payload="payload"
        :results="results"
      />
