  </Layout>


  <teleport to="#modal">
    <ModalConfirm
      v-if="loaded"
      :open="modalOpen"
      @close="modalOpen = false"
      @reload="loadProposal"
      :space="space"
      :balanceOf="balanceOf"
      :proposal="proposal"
      :id="id"
      :selectedChoice="selectedChoice"
      :totalScore="totalScore"
      :scores="scores"
      :snapshot="payload.snapshot"
    />
    <ModalStrategies
      :open="modalStrategiesOpen"
      @close="modalStrategiesOpen = false"
      :space="space"
      :strategies="space.strategies"
    />
  </teleport>
</template>

<script>

import { mapActions } from "vuex";
import { strategy as erc20BalanceOf } from "@snapshot-labs/snapshot.js/src/strategies/erc20-balance-of/index";
import { multicall } from "@snapshot-labs/snapshot.js/src/utils";
import getProvider from "@snapshot-labs/snapshot.js/src/utils/provider";


export default {
  data() {
    return {
      key: this.$route.params.key,
      id: this.$route.params.id,
      loading: false,
      loaded: false,
      voteLoading: false,
      deleteLoading: false,
      proposal: {},
      votes: {},
      results: [],
      modalOpen: false,
      modalStrategiesOpen: false,
      selectedChoice: 0,
      totalScore: 0,
      balanceOf: 0,
      scores: []
    };
  },
  computed: {
    space() {
      return this.app.spaces[this.key];
    },
    payload() {
      return this.proposal.msg.payload;
    },
    ts() {
      return (Date.now() / 1e3).toFixed();
    },
    symbols() {
      return this.space.strategies.map(strategy => strategy.params.symbol);
    }
  },
  watch: {
    "web3.account": async function(val, prev) {
      if (val?.toLowerCase() !== prev) await this.loadPower();
    }
  },
  methods: {
    ...mapActions(["getProposal", "getPower", "send", "getBalanceOf"]),
    async loadProposal() {
      const proposalObj = await this.getProposal({
        space: this.space,
        id: this.id
      });
      this.proposal = proposalObj.proposal;
      this.votes = proposalObj.votes;
      this.results = proposalObj.results;
    },
    async loadPower() {
      if (!this.web3.account || !this.proposal.address) return;
      try {
        this.balanceOf = await this.getBalanceOf({
          space: this.space,
          address: this.web3.account,
          snapshot: this.payload.snapshot
        });
        const { scores, totalScore } = await this.getPower({
          space: this.space,
          address: this.web3.account,
          snapshot: this.payload.snapshot
        });
        this.totalScore = totalScore;
        this.scores = scores;
      } catch (e) {
        console.log(e);
      }
    },
    async deleteProposal() {
      this.deleteLoading = true;
      try {
        if (
          await this.send({
            space: this.space.key,
            type: "delete-proposal",
            payload: {
              proposal: this.id
            }
          })
        ) {
          this.deleteLoading = false;
          this.$router.push({
            name: "proposals"
          });
        }
      } catch (e) {
        console.error(e);
      }
      this.deleteLoading = false;
    }
  },
  async created() {
    this.loading = true;
    await this.loadProposal();
    await this.loadPower();
    this.loading = false;
    this.loaded = true;
  }
};


</script>


