<template>
  <span class="d-inline-block v-align-middle line-height-0 img_wap">
    <img
      v-if="!error"
      :src="url"
      :style="{
        width: `${size || 22}px`,
        height: `${size || 22}px`
      }"
      @error="error = true"
      class="circle border line-height-0"
    />
    <Avatar v-else :address="spaceAddress" :size="size" />
  </span>
</template>

<script>
import { formatBytes32String } from "@ethersproject/strings";

export default {
  props: ["space", "size", "symbolIndex"],
  data() {
    return {
      error: false
    };
  },
  computed: {
    url() {
      const file = this.symbolIndex
        ? this.symbolIndex === "space"
          ? "space"
          : `logo${this.symbolIndex}`
        : "logo";
      return `https://raw.githubusercontent.com/Sunderfinance/snapshot-spaces/master/spaces/${this.space}/${file}.png`;
    },
    spaceAddress() {
      if (this.space) return formatBytes32String(this.space.slice(0, 24));
      return "";
    }
  }
};
</script>


<style lang="scss" scoped>
.img_wap {
  display: block;
  padding: 10px;
  box-sizing: border-box;
  background-color: #FCFEFF;
  border: 1px solid rgba(0, 117, 255, 0.2);border-radius: 100px;


}
</style>
