<template>

  <Container>
    <div class="mb-3 d-flex">
      <div class="flex-auto">
        <router-link
          :to="'/'"
          class="text-gray"
        >
          <Icon name="back" size="22" class="v-align-middle" />
          Back
        </router-link>
        <div class="top_name d-flex flex-items-center flex-auto">
          <h2 class="mr-2">
            {{ space.name }}
            <UiCounter
              :counter="Object.keys(proposalsWithFilter).length"
              class="ml-1"
            />
          </h2>
        </div>
      </div>

    </div>
  </Container>
  <Layout>
    <template #proposals-left>
      <div class="left_box">
        <div class="top">
          <Token
            :space="space.key"
            symbolIndex="space"
            size="48"
            class="my-3"
          />
          <p>{{ space.name }}</p>
        </div>

        <div class="bottom">

          <div class="create_space"
               v-if="!isMember"
          >
            <router-link
              :to="{ name: 'create', params: { key } }"
            >
              {{ $t("proposals.new") }}
            </router-link>
          </div>

          <div v-for="item in space.members" class="cell_widget" :key="item">
            <img class="icon" src="@/assets/images/compound.png" alt="" />
            <div>
              <h2>{{ name(item) }}</h2>
              <a
                :href="_explorer(space.network, item, 'address')"
                target="_blank"
              >
                <h3>{{ name(item) }} <img src="@/assets/images/copy_button.png" alt=""></h3>
              </a>
            </div>
          </div>
        </div>
      </div>
    </template>
    <template #proposals-right>
      <div>
        <Container :slim="true" :class="'block_wap'">
          <div class="bg_color">
            <div
              class="px-4 py-3 bg-gray-dark overflow-auto menu-tabs rounded-top-0 rounded-md-top-2 inner_top"
            >
              <router-link
                v-for="state in states"
                :key="state"
                v-text="$t(`proposals.states.${state}`)"
                :to="`/${key}/${state}`"
                :class="tab === state && 'text-white'"
                class="mr-3 text-gray"
              />
            </div>
            <div class="inner_body">
              <RowLoading v-if="loading" class="" />
              <div v-if="loaded">
                <RowProposal
                  v-for="(proposal, i) in proposalsWithFilter"
                  :key="i"
                  :proposal="proposal"
                  :space="space"
                  :verified="space.verified"
                  :i="i"
                  class=""
                />
              </div>
              <p
                v-if="loaded && Object.keys(proposalsWithFilter).length === 0"
                class="p-4 m-0  d-block"
              >
                {{ $t("proposals.noProposals") }}
              </p>
            </div>

          </div>
        </Container>
      </div>
    </template>
  </Layout>
</template>

<script>
import { mapActions } from "vuex";
import { filterProposals } from "@/helpers/utils";

export default {
  data() {
    return {
      loading: false,
      loaded: false,
      proposals: {},
      tab: "all"
    };
  },
  computed: {
    key() {
      return this.domain || this.$route.params.key;
    },
    space() {
      return this.app.spaces[this.key];
    },
    states() {
      const states = [
        "all",
        "core",
        "community",
        "active",
        "pending",
        "closed"
      ];
      return this.space.filters.onlyMembers
        ? states.filter(state => !["core", "community"].includes(state))
        : states;
    },
    totalProposals() {
      return Object.keys(this.proposals).length;
    },

    proposalsWithFilter() {
      console.log(this.proposals);
      if (this.totalProposals === 0) return {};

      return Object.fromEntries(
        Object.entries(this.proposals)
          .filter(proposal => filterProposals(this.space, proposal, this.tab))
          .sort((a, b) => b[1].msg.payload.end - a[1].msg.payload.end, 0)
      );
    },
    isMember() {
      const members = this.space.members.map(address => address.toLowerCase());
      return (
        this.$auth.isAuthenticated.value &&
        this.web3.account &&
        members.includes(this.web3.account.toLowerCase())
      );
    },
    isEns() {
      return this.key.includes(".eth") || this.key.includes(".xyz");
    }
  },
  methods: {
    ...mapActions(["getProposals"]),
    name(address) {
      return this._shorten(address);
    }
  },
  async created() {
    this.loading = true;
    this.tab =
      this.$route.params.tab || this.space.filters.defaultTab || this.tab;
    this.proposals = await this.getProposals(this.space);
    this.loading = false;
    this.loaded = true;
  }
};
</script>

